import React from "react";

function PrescriptionCards({ onClick, cards }) {
  return (
    <div className="patient__prescription-forms px-3 py-3" onClick={onClick}>
      {cards.map((card, index) => (
        <div key={index} className="patient__prescription-cards">
          {card}
        </div>
      ))}
    </div>
  );
}

export default PrescriptionCards;
