import React, { useState } from "react";
import { Card, Col, Form, Pagination, Row } from "react-bootstrap";
import { BiSearch } from "react-icons/bi";
import { useNavigate } from "react-router-dom";
import DiseaseForm from "./DiseaseForm";
import PrescriptionCards from "./PrescriptionCards";

const prescriptionCards = [
  "Acid Reflux & GERD",
  "Acne",
  "ADHD & Autistic Spectrum Disorder (ASD) Advice & Referral",
  "Allergic Rhinitis (Hay-fever)",
  "Anti-malaria Treatment (Doxycycline)",
  "Anxiety",
  "Anxiety/Depression",
  "Asthma",
  "Bacterial Vaginosis",
  "Chest & Sinus Infection",
  "Cold sore",
  "Covid 19",
  "Delay Period Pills",
  "Ear issues",
  "Early / Rapid Ejaculation",
  "Eczema (Dermatitis)",
  "EpiPen (Adrenaline 'Epinephrine' Auto-Injectors)",
  "Eye infections",
];

export default function OnlinePrescForms() {
  const [currentPage, setCurrentPage] = useState(1);
  const navigate = useNavigate();
  const searchParams = new URLSearchParams(window.location.search);
  const diseaseName = searchParams.get("diseaseName");
  const diseaseId = searchParams.get("diseaseId");

  if (diseaseId) {
    console.log("Disease Id:", diseaseId);
  } else {
    console.log("Disease parameter not found in the URL");
  }

  const cardsPerPage = 12;

  const indexOfLastCard = currentPage * cardsPerPage;
  const indexOfFirstCard = indexOfLastCard - cardsPerPage;
  const currentCards = prescriptionCards.slice(
    indexOfFirstCard,
    indexOfLastCard
  );

  const paginate = (pageNumber) => setCurrentPage(pageNumber);

  const customTotal = (from, to, size) => (
    <span>
      {from} to {to} out of {size} entries
    </span>
  );

  const handleClick = () => {
    const diseaseId = 11;
    const diseaseName = "Acne";
    navigate(
      `/patient/online-prescription-forms?diseaseName=${diseaseName}&diseaseId=${diseaseId}`
    );
  };

  return (
    <>
      {!diseaseId ? (
        <>
          <h5>Prescription Forms</h5>
          <Card className="shadow-sm mt-4">
            <>
              <Row>
                <Col xl={3} md={6} sm={6} xs={12} className="mt-3 mx-3">
                  <div className="d-flex">
                    <Form.Control
                      type="text"
                      placeholder="Search"
                      className="me-2 search-field-spacing"
                      aria-label="Search"
                    />
                    <BiSearch size={22} className="searchbar-icon" />
                  </div>
                </Col>
              </Row>
              <PrescriptionCards cards={currentCards} onClick={handleClick} />
              {prescriptionCards.length > cardsPerPage && (
                <Pagination className="d-flex justify-content-between align-items-center px-3 patient__prescription-pagination">
                  <Pagination>
                    {customTotal(
                      indexOfFirstCard + 1,
                      indexOfLastCard,
                      prescriptionCards.length
                    )}
                  </Pagination>
                  <div
                    className="d-flex justify-content-between"
                    style={{ width: "9.5rem" }}
                  >
                    <Pagination.Prev
                      onClick={() => paginate(currentPage - 1)}
                      disabled={currentPage === 1}
                    />
                    {Array.from({
                      length: Math.ceil(
                        prescriptionCards.length / cardsPerPage
                      ),
                    }).map((_, index) => (
                      <Pagination.Item
                        key={index + 1}
                        active={currentPage === index + 1}
                        onClick={() => paginate(index + 1)}
                      >
                        {index + 1}
                      </Pagination.Item>
                    ))}
                    <Pagination.Next
                      onClick={() => paginate(currentPage + 1)}
                      disabled={
                        currentPage ===
                        Math.ceil(prescriptionCards.length / cardsPerPage)
                      }
                    />
                  </div>
                </Pagination>
              )}
            </>
          </Card>
        </>
      ) : (
        <DiseaseForm diseaseName={diseaseName} />
      )}
    </>
  );
}
