import React from "react";
import { Button, Card, Col, Form, Row } from "react-bootstrap";
import { Link } from "react-router-dom";
import { IoIosArrowForward } from "react-icons/io";

function DiseaseForm({ diseaseName }) {
  return (
    <>
      <nav aria-label="breadcrumb">
        <ol className="breadcrumb">
          <li className="breadcrumb-item">
            <Link
              to={"/patient/online-prescription-forms"}
              className="text-decoration-none fs-5 color-99"
            >
              Prescription Forms
            </Link>
          </li>
          <IoIosArrowForward
            className="mx-1 mt-2 color-99"
          />
          <li
            className="breadcrumb-item active fs-5"
            aria-current="page"
            style={{ color: "#6045EB" }}
          >
            {diseaseName}
          </li>
        </ol>
      </nav>
      <Card className="shadow-sm mt-2">
        <Form>
          <Row className="m-3 d-flex justify-content-center flex-column">
            <Col
              // key={sectionIndex}
              xl={7}
              lg={10}
              xs={12}
              className="p-3 rounded mx-auto mb-3"
              style={{
                boxShadow: "0px 0px 24px 0px #0000000A",
                borderLeft: "6px solid #3F8BFC",
              }}
            >
              <Form.Group className="mb-3" controlId="formQuestion1">
                <Form.Label className="label-primary mt-2">
                  Question # 1
                </Form.Label>
                <Form.Control
                  as="textarea"
                  rows={3}
                  placeholder="Answer Text"
                  className="mt-2"
                />
              </Form.Group>
            </Col>
            <Col
              // key={sectionIndex}
              xl={7}
              lg={10}
              xs={12}
              className="p-3 rounded mx-auto mb-3"
              style={{
                boxShadow: "0px 0px 24px 0px #0000000A",
                borderLeft: "6px solid #3F8BFC",
              }}
            >
              <Form.Group className="mb-3" controlId="formQuestion1">
                <Form.Label className="label-primary mt-2">
                  Question # 2
                </Form.Label>
                <Form.Check type="checkbox" id="checkbox1" label="checkbox" />
                <Form.Check type="checkbox" id="checkbox2" label="checkbox1" />
                <Form.Check type="checkbox" id="checkbox3" label="checkbox2" />
              </Form.Group>
            </Col>
            <Col
              // key={sectionIndex}
              xl={7}
              lg={10}
              xs={12}
              className="p-3 rounded mx-auto mb-3"
              style={{
                boxShadow: "0px 0px 24px 0px #0000000A",
                borderLeft: "6px solid #3F8BFC",
              }}
            >
              <Form.Group className="mb-3" controlId="formQuestion1">
                <Form.Label className="label-primary mt-2">
                  Question # 3
                </Form.Label>
                <Form.Check
                  type="radio"
                  id="radio1"
                  name="group1"
                  label="radio"
                />
                <Form.Check
                  type="radio"
                  id="radio2"
                  name="group1"
                  label="radio"
                />
                <Form.Check
                  type="radio"
                  id="radio3"
                  name="group1"
                  label="radio"
                />
              </Form.Group>
            </Col>
            <Col
              // key={sectionIndex}
              xl={7}
              lg={10}
              xs={12}
              className="p-3 rounded mx-auto mb-3"
              style={{
                boxShadow: "0px 0px 24px 0px #0000000A",
                borderLeft: "6px solid #3F8BFC",
              }}
            >
              <Form.Group className="mb-3" controlId="formQuestion1">
                <Form.Label className="label-primary mt-2">
                  Question # 4
                </Form.Label>
                <Form.Control
                  as="textarea"
                  rows={3}
                  placeholder="Answer Text"
                  className="mt-2"
                />
              </Form.Group>
            </Col>

            <Col>
              <Button
                variant="primary"
                type="button"
                className="Admin-Add-btn fw-bold float-end"
              >
                Save
              </Button>
            </Col>
          </Row>
        </Form>
      </Card>
    </>
  );
}
export default DiseaseForm;
